.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	vertical-align: top;
	padding: 15px;
	min-width: 120px;
	border: none;
	outline: none;
	color: $white;
	background: $blue;
	font-family: inherit;
	@include f(18px, 300, 20px);
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	transition: opacity .175s, background .175s, box-shadow .175s;
	@include metro {
		background: $metroSecondary; }
	@include boost {
		background: $elevatePrimary; }
	&:active {
		color: $white;
		background: darken($blue, 10%);
		box-shadow: inset 2px 2px 6px rgba(0,0,0,.2);
		@include metro {
			background: darken($metroSecondary, 10%); }
		@include boost {
			background: darken($elevatePrimary, 10%); } }
	&:disabled,
	&.is-disabled {
		opacity: 0.7;
		cursor: not-allowed; }
	&_sm {
		padding: 12px 15px;
		font-size: 14px;
		line-height: 16px; }
	&_gray {
		background: #bec1cb;
		@include metro {
			background: #bec1cb; }
		@include boost {
			background: #bec1cb; }
		&:active {
			background: darken(#bec1cb, 10%);
			@include boost {
				background: darken(#bec1cb, 10%); }
			@include metro {
				background: darken(#bec1cb, 10%); } } }
	&_red {
		background: $red;
		@include metro {
			background: $red; }
		&:active {
			background: darken($red, 10%);
			@include metro {
				background: darken($red, 10%); } } }
	&_wide {
		width: 100%; }
	&_plus {
		width: 40px;
		min-width: auto;
		height: 40px;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			width: 18px;
			height: 1px;
			background: currentColor;
			transform: translate(-50%,-50%); }
		&:after {
			transform: translate(-50%,-50%) rotate(90deg); } } }

.btn-sync {
	width: 170px;
	background: none;
	font-size: 0;
	user-select: none;
	&:disabled {
		cursor: not-allowed;
		.btn-sync {
			&__active {
				display: none; }
			&__inactive {
				display: block; } } }
	&:active {
		.btn-sync {
			&__active {
				color: $white;
				background: darken($blue, 10%);
				box-shadow: inset 2px 2px 6px rgba(0,0,0,.2);
				@include metro {
					background: darken($metroSecondary, 10%); }
				@include boost {
					background: darken($elevatePrimary, 10%); } } } }
	&__icon {
		display: block;
		margin-bottom: 10px;
		width: 100%; }
	&__text {
		padding: 10px;
		color: #5d6366;
		background: #bfc0c3;
		font-size: 16px;
		font-weight: 500;
		text-transform: uppercase;
		transition: color .175s, background .175s, box-shadow .175s; }
	&__active {
		display: block;
		color: $white;
		background: $blue;
		@include metro {
			background: $metroSecondary; }
		@include boost {
			background: $elevatePrimary; } }
	&__inactive {
		display: none; } }
